import ReactGA from 'react-ga4';

// Initialize Google Analytics
export const initGA = () => {
  const measurementId = process.env.REACT_APP_GA_MEASUREMENT_ID;
  
  if (measurementId) {
    ReactGA.initialize(measurementId);
    console.log('Google Analytics initialized with ID:', measurementId);
  } else {
    console.warn('Google Analytics measurement ID not found. Set REACT_APP_GA_MEASUREMENT_ID environment variable.');
  }
};

// Track page views
export const trackPageView = (path) => {
  ReactGA.send({ hitType: 'pageview', page: path });
};

// Track custom events
export const trackEvent = (category, action, label = null, value = null) => {
  ReactGA.event({
    category,
    action,
    label,
    value,
  });
};

// Track specific app events
export const trackAppEvent = {
  // File operations
  fileUpload: (fileCount) => trackEvent('File', 'Upload', `Files: ${fileCount}`),

  // Graph operations
  graphGenerate: (preset) => trackEvent('Graph', 'Generate', preset || 'Custom'),
  graphInspect: (methodName) => trackEvent('Graph', 'Inspect', methodName),
  
  // Demo interactions
  demoPresetSelect: (presetName) => trackEvent('Demo', 'PresetSelect', presetName),
  githubLinkClick: () => trackEvent('Demo', 'GithubLinkClick'),
 
  // Error tracking
  error: (errorType, errorMessage) => trackEvent('Error', errorType, errorMessage),
};
